.backgroundSection {
    width: 100%;
    height: calc(100vh - 190px);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; 
    position: relative;
    overflow: hidden; 
  }
  
  
  .title {

    position: absolute;
    bottom: 100px;
    left: 100px;
    margin: 0;
    color: white;
  }
  